import React from 'react'
import { lady, right } from '../../../assets/partner'

const reasons = [
    {
        heading: "Improved Guest Experience",
        paragraph: "Stress-free VIP luggage pickup, delivery, storage, and lost & found services for your customers"
    },
    {
        heading: "Extra Revenue",
        paragraph: "Earn a revenue share of up to 25% anytime your referral  uses our service"
    },
    {
        heading: "Free Service",
        paragraph: "You do not have to pay to use our service. Its absolutely free. No expense to your business."
    },
    {
        heading: "Boosted Loyalty",
        paragraph: "Using our service means guest satisfaction. Enhanced satisfaction leads to repeat bookings."
    }
]

const Reasons = () => {
    return (
        <section className="mt-20 w-[90vw] mx-auto">

            <h1 className='text-center text-5xl font-bold '>Being a partner has its benefits</h1>

            <div className='mt-24 grid grid-cols-10 items-center gap-16'>

                <div className='col-span-3'>
                    <img src={lady} alt="lady" className='!w-full' />
                </div>

                <div className='col-span-7 grid grid-cols-2 gap-24'>

                    {reasons.map((reason, i) => (
                        <div key={i}>
                            <img src={right} className='w-14' alt="right" />
                            <h1 className='mt-4 text-[28px] font-bold'>{reason.heading}</h1>
                            <p className='mt-4 text-gray-700 text-xl w-[85%]'>{reason.paragraph}</p>
                        </div>
                    ))}
                </div>
            </div>

        </section>
    )
}

export default Reasons
