import React from 'react'
import { count1, count2, count3 } from '../../../assets/partner'

const works = [
    {
        img: count1,
        paragraph: "Register Your Company"
    },
    {
        img: count2,
        paragraph: "Well Review Your Profile"
    },
    {
        img: count3,
        paragraph: "Approval & Start Earning"
    },
]

const HowitWorks = () => {
    return (
        <section className='w-[90vw] mx-auto mt-36'>
            <h1 className='text-center text-5xl font-bold '>{"Heres"} how it works</h1>
            <div className='grid grid-cols-3 gap-20 mt-16'>
                {works.map((work, i) => (
                    <div key={i} className={`h-[300px] ${work.img === count1 && "bg-[#FF5757]"} ${work.img === count2 && "bg-[#545454]"} ${work.img === count3 && "bg-[#FFDE59]"} flex items-center justify-center`}>
                        <div className='w-full'>
                            <img src={work.img} alt={i + 1} className='w-[30%] mx-auto' />
                            <p className='text-2xl text-white font-medium text-center mt-5'>{work.paragraph}</p>
                        </div>
                    </div>
                ))}
            </div>
        </section>
    )
}

export default HowitWorks