import React, { useState } from 'react';
import { Box, Container, Typography, Accordion, AccordionSummary, AccordionDetails } from '@mui/material';
import { Add } from '@mui/icons-material';

const FAQ = () => {
  const [expanded, setExpanded] = useState(false);

  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  return (
    <Box sx={{ color: '#fff' }} id="partner" className="!mt-28 !mb-16">
      <Container maxWidth="md">
        <Typography variant="h4" component="h2" gutterBottom align="center" sx={{ mb: 3 }} className='!text-[3rem] !font-black'>
          Frequently Asked Questions
        </Typography>
        <Accordion
          expanded={expanded === 'panel1'}
          onChange={handleChange('panel1')}
          sx={{ backgroundColor: '#2D2D2D', color: '#fff', mb: 1 }}
        >
          <AccordionSummary
            expandIcon={<Add sx={{ color: '#fff' }} fontSize='large' />}
            sx={{ padding: '0.5rem 1.25rem 0.5rem 1.25rem' }}
            className='hover:!bg-[#414141] transition-all duration-100'
          >
            <Typography sx={{ fontSize: '1.45rem' }}>What is BaggageClaimed?</Typography>
          </AccordionSummary>
          <AccordionDetails className='!border-t-2 !border-black !py-[0.85rem] !px-[1.25rem]'>
            <Typography className='!text-xl'>
              BaggageClaimed offers VIP luggage storage and transfer services at multiple airports. Our goal is to provide a seamless and luxurious experience for travelers by taking care of their baggage needs.
            </Typography>
            <Typography className='!text-xl !mt-4'>
              Whether you need temporary storage for your luggage while you explore the city or a transfer service to get your bags to your next destination, BaggageClaimed has you covered. Our professional team ensures that your belongings are handled with care and delivered on time.
            </Typography>
          </AccordionDetails>
        </Accordion>
        <Accordion
          expanded={expanded === 'panel2'}
          onChange={handleChange('panel2')}
          sx={{ backgroundColor: '#2D2D2D', color: '#fff', mb: 1 }}
        >
          <AccordionSummary
            expandIcon={<Add sx={{ color: '#fff' }} fontSize='large' />}
            sx={{ padding: '0.5rem 1.25rem 0 1.25rem' }}
            className='hover:!bg-[#414141] transition-all duration-100'
          >
            <Typography sx={{ fontSize: '1.45rem' }}>How much does BaggageClaimed cost?</Typography>
          </AccordionSummary>
          <AccordionDetails className='!border-t-2 !border-black !py-[0.85rem] !px-[1.25rem]'>
            <Typography className='!text-xl'>
              The cost of BaggageClaimed services depends on the specific services you choose. We offer a range of options to suit different needs and budgets.
            </Typography>
            <Typography className='!text-xl !mt-4'>
              For example, our luggage storage rates are based on the duration of storage and the size of your bags, while our transfer services are priced based on the distance and the urgency of delivery. For detailed pricing information and to get a personalized quote, please contact our support team or visit our pricing page on the website.
            </Typography>
          </AccordionDetails>
        </Accordion>
        <Accordion
          expanded={expanded === 'panel3'}
          onChange={handleChange('panel3')}
          sx={{ backgroundColor: '#2D2D2D', color: '#fff', mb: 1 }}
        >
          <AccordionSummary
            expandIcon={<Add sx={{ color: '#fff' }} fontSize='large' />}
            sx={{ padding: '0.5rem 1.25rem 0 1.25rem' }}
            className='hover:!bg-[#414141] transition-all duration-100'
          >
            <Typography sx={{ fontSize: '1.45rem' }}>Where can I use BaggageClaimed services?</Typography>
          </AccordionSummary>
          <AccordionDetails className='!border-t-2 !border-black !py-[0.85rem] !px-[1.25rem]'>
            <Typography className='!text-xl'>
              BaggageClaimed services are available at multiple airports across the country. We have strategically located our service points to ensure maximum convenience for travelers.
            </Typography>
            <Typography className='!text-xl !mt-4'>
              To find out if we operate at your airport of interest, please visit our locations page on the website. There, you will find a comprehensive list of all the airports where BaggageClaimed is available, along with detailed information about the services offered at each location.
            </Typography>
          </AccordionDetails>
        </Accordion>
        <Accordion
          expanded={expanded === 'panel4'}
          onChange={handleChange('panel4')}
          sx={{ backgroundColor: '#2D2D2D', color: '#fff', mb: 1 }}
        >
          <AccordionSummary
            expandIcon={<Add sx={{ color: '#fff' }} fontSize='large' />}
            sx={{ padding: '0.5rem 1.25rem 0 1.25rem' }}
            className='hover:!bg-[#414141] transition-all duration-100'
          >
            <Typography sx={{ fontSize: '1.45rem' }}>How do I book a service?</Typography>
          </AccordionSummary>
          <AccordionDetails className='!border-t-2 !border-black !py-[0.85rem] !px-[1.25rem]'>
            <Typography className='!text-xl'>
              Booking a service with BaggageClaimed is simple and straightforward. You can book our services online through our user-friendly website. Just navigate to the booking section, choose the service you need, and follow the prompts to complete your reservation.
            </Typography>
            <Typography className='!text-xl !mt-4'>
              Alternatively, if you prefer personalized assistance or have specific questions, you can contact our support team via phone or email. Our team is always ready to help you with the booking process and ensure that all your requirements are met.
            </Typography>
          </AccordionDetails>
        </Accordion>
        <Accordion
          expanded={expanded === 'panel5'}
          onChange={handleChange('panel5')}
          sx={{ backgroundColor: '#2D2D2D', color: '#fff', mb: 1 }}
        >
          <AccordionSummary
            expandIcon={<Add sx={{ color: '#fff' }} fontSize='large' />}
            sx={{ padding: '0.5rem 1.25rem 0 1.25rem' }}
            className='hover:!bg-[#414141] transition-all duration-100'
          >
            <Typography sx={{ fontSize: '1.45rem' }}>What if I have special requirements?</Typography>
          </AccordionSummary>
          <AccordionDetails className='!border-t-2 !border-black !py-[0.85rem] !px-[1.25rem]'>
            <Typography className='!text-xl'>
              At BaggageClaimed, we understand that every traveler has unique needs. Thats why we offer custom solutions to fit your specific requirements. Whether you need assistance with oversized luggage, fragile items, or have other special requests, our team is here to help.
            </Typography>
            <Typography className='!text-xl !mt-4'>
              Please reach out to our support team with your details, and we will work with you to create a tailored solution that meets your needs. Our commitment is to provide exceptional service and ensure your complete satisfaction.
            </Typography>
          </AccordionDetails>
        </Accordion>
      </Container>
    </Box>
  );
};

export default FAQ;
